.boton-contenedor{
    height: 1.4em;
    display: flex;
    align-items: center;
    justify-content: center;
    font-weight: bold;
    font-size: 38px;
    background-color: #1b1b32;
    color: white;
    border-radius: 25px;
    border: 2px solid white;
    margin: 4px;
    cursor: pointer;
    user-select: none;
    flex: 1 1;
}
.operador{
    background-color: #00741b;
}
.boton-contenedor:hover{
    background-color: #5a01a7; 
}
.operador:hover{
    background-color: #ff0600;
}